<template>
  <div id="app">
    <div v-if="loading" class="loading">Loading...</div>
    <div v-else>
      <div class="settings">
        <div class="settings-header">
          <div class="title">Random Commanders</div>
          <div class="commander-count-wrapper">
            <label for="commander-count">#</label>
            <input type="number" id="commander-count" v-model.number="commanderCount" min="1" max="10" class="input" />
          </div>
        </div>

        <div class="content" v-show="settingsVisible">
          <div class="setting">
              <div class="title">Colors</div>
              <label class="checkbox"><input type="checkbox" v-model="selectedColors.u" /> Blue </label>
              <label class="checkbox"><input type="checkbox" v-model="selectedColors.b" /> Black </label>
              <label class="checkbox"><input type="checkbox" v-model="selectedColors.w" /> White </label>
              <label class="checkbox"><input type="checkbox" v-model="selectedColors.r" /> Red </label>
              <label class="checkbox"><input type="checkbox" v-model="selectedColors.g" /> Green </label>
              <label class="checkbox"><input type="checkbox" v-model="multicolorOnly" /> Only Multicolor </label>
          </div>

          <div class="setting">
            <div class="title">Restriction</div>
            <select v-model="restriction" class="input">
              <option value="none">None</option>
              <option value="partners">Only Partners</option>
              <option value="backgrounds">Only Backgrounds</option>
            </select>
          </div>

          <div class="setting">
            <div class="title">Rarity</div>
            <select v-model="selectedRarity" class="input">
              <option value="any">Any</option>
              <option value="u">Uncommon</option>
              <option value="m">Mythic</option>
            </select>
          </div>

        </div>

        <div class="settings-footer">
          <button @click="fetchCommanders" class="btn blue">Get Commanders</button>
          <button @click="toggleSettings" class="btn toggle">
            <span v-if="settingsVisible">▲ Hide Settings</span>
            <span v-else>▼ Show Settings</span>
          </button>
        </div>
      </div>

      <div class="commanders" v-if="commanders.length > 0">
        <div v-for="commander in commanders" :key="commander.id" class="commander-card">          

          <div class="commander-img-wrapper">
            <a :href="commander.scryfall_uri" target="_blank" class="commander-link">
              <img :src="getImageUrl(commander)" alt="commander image" class="commander-img" crossorigin="anonymous">
            </a>
          </div>

          <div class="commander-info">            
            <h2>{{ commander.name }}</h2>
            <div class="commander-info-prices">
              <div v-if="commander.prices.usd" class="commander-info-price">
                ${{ commander.prices.usd }}
              </div>
              <div v-if="commander.prices.usd_foil" class="commander-info-price foil">
                *${{ commander.prices.usd_foil }}
              </div>
              <div v-if="commander.prices.usd_etched" class="commander-info-price etched">
                **${{ commander.prices.usd_etched }}
              </div>
            </div>            
            
            <button v-if="commander.card_faces && commander.card_faces.length > 1 && commander.card_faces[0].image_uris" 
                    @click="toggleFace(commander)" 
                    class="btn green small">
              Transform
            </button>
          </div>
        </div>
      </div>

      <div v-if="commanders.length > 0" class="generate-image-button-container">
        <button @click="generateImage" class="btn green">
          Generate Image of Commanders
        </button>
      </div>
    </div>

    <canvas ref="canvas" style="display: none;"></canvas>
  </div>
</template>

<script>
export default {
  data() {
    return {
      commanders: [],
      loading: false,
      commanderCount: 5,
      selectedColors: {
        u: false,
        b: false,
        w: false,
        r: false,
        g: false,
        c: false,
      },
      multicolorOnly: false,
      restriction: "none",
      selectedRarity: "any",
      lastApiCallTime: 0,
      settingsVisible: false,
    };
  },
  methods: { 
    toggleSettings() {
      this.settingsVisible = !this.settingsVisible; // Toggle the visibility of the settings
    },

    toggleFace(commander) {
      commander.isTransformed = !commander.isTransformed;
    },
    
    getQueryFilter() {
        let colorFilter = '';
        const selectedColorKeys = Object.entries(this.selectedColors)
          .filter(([color, selected]) => selected) 
          .map(([color]) => color).join(''); 

        if (selectedColorKeys) {
          if(!this.multicolorOnly ) {
            const colorlessQuery = !this.selectedColors.c ? "%20-id=c" : "";
            colorFilter = `%20(id<=${selectedColorKeys}${colorlessQuery})`;
          } else {
            colorFilter = `%20id=${selectedColorKeys}`;
          }
        } else {
          if(this.multicolorOnly)
            colorFilter = `%20c>1`
        }

        let rarityFilter = '';
        if (this.selectedRarity !== 'any') {
          rarityFilter = `%20rarity:${this.selectedRarity}`;
        }
        
        let restrictorFilter = '';
        if(this.restriction == "backgrounds") {
          restrictorFilter = `%20o:"Choose a background"`;
        } else if (this.restriction == "partners") {
          restrictorFilter = `%20o:/^partner$/`;
        }

        return {
          color: colorFilter,
          rarity: rarityFilter,
          full:`${colorFilter}${rarityFilter}${restrictorFilter}`
        };
    },
    async fetchCommanders() {
      this.loading = true;
      if (this.settingsVisible) {
        this.settingsVisible = false; // Hide the settings when picking commanders
      }

      try {
        var queryFilter = this.getQueryFilter();

        const commanders = [];
        for (let i = 0; i < this.commanderCount; i++) {
          const commander = await this.fetchRandomCommander(queryFilter.full);
          commanders.push(commander);
        }
        
        const partnerCount = commanders.filter(commander =>
          commander.keywords && commander.keywords.includes("Partner") && !commander.keywords.includes("Partner with")
        ).length;

        if (partnerCount === 1) {
          const randomPartner = await this.fetchRandomPartner(queryFilter.rarity);
          commanders.push(randomPartner);
        }

        const backgroundCount = commanders.filter(commander => commander.keywords && commander.keywords.includes("Choose a background")).length;
        const backgroundsToGet = this.restriction == "backgrounds" ? this.commanderCount : (backgroundCount > 0 ? 1 : 0);
        if(backgroundsToGet > 0) {
          for (let i = 0; i < backgroundsToGet; i++) {
            const randomBackground = await this.fetchRandomBackground(queryFilter.rarity);
            commanders.push(randomBackground);
          }
        }

        this.commanders = commanders;
        this.loading = false;
      } catch (error) {
        console.error("Error fetching commanders:", error);
        this.loading = false;
      }
    },

    async ensureDelay(ms) {
      const currentTime = Date.now();
      const timeSinceLastCall = currentTime - this.lastApiCallTime;
      const remainingTime = Math.max(0, ms - timeSinceLastCall);
      if (remainingTime > 0) {
        await new Promise(resolve => setTimeout(resolve, remainingTime)); 
      }
      this.lastApiCallTime = Date.now();
    },
    async fetchRandom(queryFilter) {
      await this.ensureDelay(100);
      const response = await fetch(`https://api.scryfall.com/cards/random?q=${queryFilter}%20game:paper`);
      const data = await response.json();
      return data;
    },
    async fetchRandomCommander(queryFilter) {
      return this.fetchRandom(`is:commander%20-t:background${queryFilter}`);
    },
    async fetchRandomPartner(queryFilter) {
      return this.fetchRandom(`o:/^partner$/${queryFilter}`);
    },
    async fetchRandomBackground(queryFilter) {
      return this.fetchRandom(`t:background${queryFilter}`);
    },

    getImageUrl(commander) {
      if (commander.image_uris?.normal) 
        return commander.image_uris.normal;

      if (commander.card_faces && commander.card_faces.length > 1) {
        return commander.isTransformed
          ? commander.card_faces[1].image_uris.normal
          : commander.card_faces[0].image_uris.normal;
      }
      return 'https://via.placeholder.com/400x600';
    },

    async generateImage() {
      const canvas = this.$refs.canvas;
      const ctx = canvas.getContext("2d");

      const imagesPerRow = 3;
      const commanderCount = this.commanders.length;
      const rows = Math.ceil(commanderCount / imagesPerRow); 
      const width = 400;
      const height = 600;
      canvas.width = imagesPerRow * width; 
      canvas.height = rows * height;         
      
      const images = document.querySelectorAll('.commander-img');
      let count = 0;
      for (const img of images) {
        await new Promise(resolve => {
          if (img.complete) resolve();
          else img.onload = resolve;
        });        
        const x = (count % imagesPerRow) * width;
        const y = Math.floor(count / imagesPerRow) * height;
        ctx.drawImage(img, x, y, width, height);
        count++;
      }
      this.downloadImage(canvas);
    },

    downloadImage(canvas) {
      const link = document.createElement('a');
      link.href = canvas.toDataURL(); 
      link.download = 'commanders_image.png'; 
      link.click(); 
    }
  },
};
</script>

<style scoped>


.loading {
  text-align: center;
  font-size: 1.5em;
  color: #007BFF;
}

.settings {
  border: 1px solid  #333;
  border-radius: 8px;
  margin: 20px auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.6);
  margin-bottom: 40px; 
  background-color: #2a2a2a; 
}

.settings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #333;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.settings-header .title {  
  font-weight: bold;
  font-size: 1.5em;
  color: #fff;
}

.setting {
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  padding: 12px 0 14px 0;
}

.setting .title {  
  min-width: 120px;
  font-size: 1.3em;
  font-weight: bold;
  color: #ddd;  
  line-height: 20px;
  padding: 0 8px 0 0;
  border: 1px solid transparent;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  margin: 0 0 5px 0;
}

.commander-count-wrapper {
  display: flex;
  align-items: center;
}

.commander-count-wrapper label {
  margin-right: 5px;
}

.commander-count-wrapper .input {
  color: #333;
  background-color: white;
  width: 50px;
}

.input {
  font-size: 1.1em;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  transition: border-color 0.3s ease;
}

.input:focus {
  border-color: #007BFF;
  outline: none;
}

.content {
  padding: 10px;
  font-size: 14px;
}

.checkbox {
  font-size: 1.1em;
  display: inline-block;
  margin-right: 15px;
}

.checkbox input[type="checkbox"]:checked + span {
  color: #007BFF;
}

.btn {
  margin-top: 10px;
  padding: 12px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.1em;
  transition: background-color 0.3s ease;
}

.btn .small {
  display: inline-block;  
  padding: 8px 16px;
  font-size: 1em;
}

.badge {
  margin-top: 10px;
  background-color: #AEAEAE;
  color: white;
  padding: 5px 15px;
  border-radius: 5px;
 
  display: inline-block;
  text-align: center;
  font-weight: bold;
}

.badge span {
  display: block;
}

.blue {
  color: white;
  background-color: #007BFF;
}

.blue:hover {
  background-color: #0056b3;
}

.green {
  background-color: #4CAF50;
  color: white;
}

.green:hover {
  background-color: #779978;
}

.money {
  background-color: #779978;
}

.commanders {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.commander-card {
  background-color: #333;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.2s ease;
}

.commander-card:hover {
  transform: scale(1.05);
}

.commander-card a {
  text-decoration: none;
  color: inherit;
}

.commander-info {
  margin-top: 15px;
}

.commander-card h2 {
  font-size: 1.2em;
  color: #fff;
  margin-bottom: 5px;
  margin-block: 5px;
  padding-bottom:10px;
}

.commander-card p {
  font-size: 1em;
  color: #bbb;
  margin-bottom: 10px;
}

.commander-img-wrapper {
  position: relative;
  display: inline-block;
}

.commander-img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  transition: visibility 0.3s ease;
}

.commander-img.second-face {
  visibility: hidden;
}

.commander-img-wrapper:hover .commander-img.second-face {
  visibility: visible;
}

.generate-image-button-container {
  text-align: center;
  margin-top: 50px;
}

.commander-info-prices {
  /* display: flex; */
  /* justify-content: space-between; */
  align-items: center;
  margin: 0 0 4px 0;
  width: 100%;
  justify-content: flex-start;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  width: auto;
  position: relative;
  cursor: pointer;
  white-space: nowrap;
  padding: 0 10px;
  height: 28px;
  font-size: 14px;
  line-height: 18px;
  font-weight: bold !important;
  text-decoration: none !important;  

  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top:10px;
}

.commander-info-price {
  flex: 1; /* Ensure each price takes equal space */
  text-align: center; /* Center the price */
}

.commander-info-price+.commander-info-price {
  border-left: 1px solid rgba(255, 255, 255, 0.1);
  padding-left: 10px;
}

.settings-toggle {
  cursor: pointer;
  font-size: 1.2em;
  text-align: right;
  margin: 10px;
  color: #007BFF;
}

.settings-toggle:hover {
  color: #0056b3;
}

.settings-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.settings-footer .btn {
  margin-right: 10px;
}

.settings-footer .toggle {
  background-color: transparent;
  border: none;
  font-size: 1.5em;
  cursor: pointer;
  color: #007BFF;
}

.settings-footer .toggle:hover  {
  color: #0056b3;
}
</style>
